.cat-page {
  background: #fff;
  &__aside {
    min-width: 0;
    margin-bottom: 20px
  }
  &__filter--mobile {
    display: none;
    @media(max-width: $bp2) {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 15px 0;
        text-align: center;
        background: $orange; 
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
        color: #fff;
        text-shadow: 1px 1px 1px #000;
        line-height: 1.8;
    }
    &:after {
      content: "";
      display: block;
      width: 18px;
      height: 18px;
      background: url(../assets/images/icons/filter.svg) center / contain no-repeat;
      margin-left: 10px;
      pointer-events: none
    }
  }
  .search {
    margin-bottom: 20px;
  }
  .pagination {
    margin: 20px 0;
    justify-content: right;
    @media (max-width: $bp3) {
      justify-content: center
    }
  }
  @media (min-width: $bp2) {
    display: flex;
    &__aside {
      flex: 0 0 250px;
    }
    &__content {
      flex: 1 1 auto;
      padding-left: 15px;
    } 
  }
}

.filter-cat {
  &__inner {
    background-color: $maincolor;
  } 
  &__header {
    height: 30px;
    background-color: $orange;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  &__icon {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 10px;
    &:before, &:after {
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: #fff;
      position: absolute;
      top: 50%;
      transform: scale(0)
    } 
  }
  &__title {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    color: #fff;
    text-shadow: 1px 1px 1px #000
  }
  &__text {
    padding: 20px 10px;
    color: #fff;
    font-size: 16px;
  }
  @media (max-width: $bp2) {
    visibility: hidden;
    opacity: 0;
    transition: all .3s linear;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    overflow: scroll;
    background-color: rgba(2, 53, 70, .5);
    &__inner {
      width: 100%;
      height: auto;
      transform: translateX(-100%);
      transition: all .3s linear;
      @media (min-width: 330px) {
        max-width: 300px;
      }
    }
    &.active {
      visibility: visible;
      opacity: 1;
      .filter-cat__inner {
        transform: translateX(0)
      }
      .filter-cat__icon {
        &::before{
          transform: translateY(-50%) rotate(-45deg) scale(1);
        }
        &::after{
          transform: translateY(-50%) rotate(45deg) scale(1);
        }
      }
    }
  }
}
.form-filter {
  &__section--price {
    padding: 10px;
    border-bottom: 1px solid #697579;
  }
  &__footer {
    padding: 20px;
  }
  &__btn {
    width: 100%;
    height: 30px;
    font-size: 16px;
    font-weight: 700;
    transition: all .5s ease;
    &:hover {
      background-color: $orange
    }
  }
}
.section-filter {
  overflow: hidden;
  position: relative;
  &__header {
    padding: 0 5px;
    border-bottom: 1px solid #697579;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 2;
    cursor: pointer;
    &:after {
      content: "";
      width: 9px;
      height: 9px;
      border-bottom: 2px solid #fff;
      border-left: 2px solid #fff;
      transform: rotate(-45deg);
      position: relative;
      top: 1px;
      cursor: pointer;
      transition: all .3s ease;
    }
    &.active:after {
      transform: rotate(135deg);
    }
  }
  &__title {
    font-size: 17px;
    color: #fff;
    line-height: 1.8;
  }
  &__body {
    height: 0; 
    overflow: hidden;
    transition: all .5s ease-out;
  }
  &__items {
    padding: 5px 17px;
  }
  &__item {
    &.opacity {
      pointer-events: none;
      cursor: none;
      opacity: 0.3
    }
  }
  &__input {
    display: none;  
  }
  &__label {
    font-size: 14px;
    line-height: 1.6;
    color: #fff;
    display: flex;
    align-items: center;
    position: relative;
    cursor: pointer;
    &:before {
      content: "";
      width: 14px;
      height: 14px;
      background-color: #fff;
      margin-right: 22px;
      cursor: pointer;
    }
    &:after {
      display: none;
      content: "";
      width: 7px;
      height: 7px;
      background-color: $orange;
      border-radius: 50%;
      position: absolute;
      left: 3.5px;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
    }
  }
  &__count {
    opacity: 0.7
  }
  &__input:checked + .section-filter__label:after {
    display: block;    
  }
}
.price-filter {
  &__slider {
    height: 2px;
    position: relative;
    margin: 10px 7px 15px;
    &:before, &:after {
      content: "";
      display: block;
      background: #fff;
      position: absolute;
      top: 0;
      bottom: 0;
      width: 5px;
    }
    &::before {
      left: -5px
    }
    &::after {
      right: -5px
    }
    .noUi-target {
      -webkit-touch-callout: none;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      touch-action: none;
      user-select: none;
      box-sizing: border-box;
      position: relative;
      background-color: #fff;
    }
    .noUi-base, .noUi-connects {
      width: 100%;
      height: 2px;
      position: relative;
      z-index: 1;
      cursor: pointer
    }
    .noUi-connects {
      overflow: hidden;
      z-index: 0;
    }
    .noUi-connect {
      height: 100%;
      width: 100%;
      background-color: $orange
    }
    .noUi-connect, .noUi-origin {
      will-change: transform;
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      -webkit-transform-style: preserve-3d;
      transform-origin: 0 0;
      transform-style: flat;
    }
    .noUi-origin {
      height: 10%;
      width: 10%;
    }
    .noUi-horizontal .noUi-handle {
      position: absolute;
      right: -5px;
      width: 10px;
      height: 10px;
      background-color: $orange;
      border-radius: 50%;
      top: -5px;
      cursor: pointer;     
    }
    .noUi-state-tap .noUi-connect,
    .noUi-state-tap .noUi-origin {
      transition: transform 0.3s;
    }
  }
  &__values {
    display: flex;
  }
  &__item {
    display: flex;
    input {
      margin: 0 15px;
      max-width: 60px;
      font-size: 14px;
      color: #000;
      padding: 0 5px;
    }
  }
  &__label {
    font-size: 14px;
    color: #fff;
  }
}
.search {
  border: 1px solid $maincolor;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__label {
    flex: 0 0 113px;
    height: 30px;
    background-color: $maincolor;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    font-style: italic;
    line-height: 2.1;
    padding-left: 5px;
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
    span {
      flex: 0 0 35px;
      height: 30px;
      background: url("../assets/images/icons/search.svg") center no-repeat;
    }
  }
  &__input {
    flex: 1 1 auto;
    height: 30px;
    input {
      width: 100%;
      height: 100%;
      padding-left: 5px;
      &:focus {
        outline: none;
      }
    }
  }
}
.choice-filter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  &__item {
    padding: 5px 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px 10px 0;
    cursor: pointer;
    &:hover {
      .choice-filter__text {
        color: $orange
      }
      svg {
        fill: $orange
      }
    }
    svg {
      width: 10px;
      height: 10px;
      fill: $maincolor;
      pointer-events: none;
      transition: all .5s ease
    }
  }
  &__text {
    font-size: 16px;
    line-height: 1.8;
    margin-right: 5px;
    transition: all .5s ease
  }
}

.catalog {
  &__items {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    @media (max-width: $bp1) {
      max-width: 870px;
      grid-template-columns: 1fr 1fr 1fr;
    }
    @media (max-width: $bp3) {
      max-width: 562px;
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: $bp4) {
      max-width: 271px;
      grid-template-columns: 1fr;
    }
  }
}