.item-catalog {
  background-color: #fff;
  border: 1px solid #d6d8e1;  
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__preview {
    padding: 5px 30px 0px;
  }
  &__image {
    padding-top: 100%;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      max-width: 100%;
      max-height: 100%;
      transition: all 1s ease;
      &:hover {
        transform: scale(1.08)
      }
    }  
  }
  &__body {
    flex: 1 1 auto;
    display: grid;
    grid-template-rows: 48px auto 48px 48px;
    text-align: center;
    background: url("../assets/images/item-bg.png");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    padding-top: 10px;
  }
  &__title {
    padding: 0 10px;
    font-size: 20px;
    font-weight: 700;
    color: $maincolor;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    &:hover {
      text-decoration: underline
    }
  }
  &__details {
    padding: 10px 10px;
    align-self: center;
  }
  &__detail{
    margin-bottom: 10px;
    font-size: 14px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__price {
    font-size: 24px;
    line-height: 2;
    font-weight: 700;
    color: $orange;
    border-bottom: 1px solid $maincolor;
  }
  &__btn {
    align-self: center;
    justify-self: center;
    padding: 5px 10px;
    font-size: 14px;
    &:hover {
      background-color: $orange;
    }
  }
}
