.rec-catalog {
  &__inner {
    padding: 10px 40px 20px;
    position: relative;
    max-width: 362px;
    margin: 0 auto;
    @media (min-width: 520px) {
      max-width: 652px
    }
    @media (min-width: $bp3) {
      max-width: 960px;
    }
    @media (min-width: $bp2) {
      max-width: none;
    }
  }
  &__title {
    font-size: 30px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 30px;
    @media (max-width:550px) {
      font-size: 24px;
    }
  }
  &__swiper {
    min-width: 0;
    overflow: hidden;
    margin: 0 auto 20px;
    max-width: 281px;
    @media (min-width: 520px) {
      max-width: 572px
    }
    @media (min-width: $bp3) {
      max-width: 880px;
    }
    @media (min-width: $bp2) {
      max-width: none;
    }
    .swiper-wrapper {
      display: flex
    }
  }
  &__slide {
    flex-basis: 100%;
    flex-grow: 0;
    flex-shrink: 0;
    display: grid;
    @media (min-width: 520px) {
      flex-basis: calc((100% - 18px)/2);
      margin-right: 18px;
      &:last-child {
        margin-right: 0;
      };
    }
    @media (min-width: $bp3) {
      flex-basis: calc((100% - 36px)/3);
    }
    @media (min-width: $bp2) {
      flex-basis: calc((100% - 54px)/4);
    }
  }
  &__nav {
    display: block;
    position: absolute;
    top: 50%;
    z-index: 2;
    transform: translateY(-50%);
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
    svg {
      width: 30px;
      height: 30px;
      fill: $violet;
      transition: all .5s ease;
      &:hover {
        fill: $orange
      }
    }
  }
  &__nav--prev {
    left: 0;
  }
  &__nav--next {
    right: 0;
  }
  &__footer {
    text-align: center;    
    a {
      width: 100%;
      max-width:410px;
      font-size: 20px;
      font-weight: 700;
      line-height: 2.1;
      transition: all .5s ease;
      &:hover {
        background-color: $orange
      }
    }
  }
}
.swiper-button-disabled {
  cursor: default;
  opacity: 0.6;
  pointer-events: none;
}