.footer {
  //position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background: url(../assets/images/footer.jpg) bottom center no-repeat ,$maincolor;
  padding-top: 30px;
  &__container {
    @media (min-width: $bp2) {
      display: flex;
    }
  }
  &__column {
    margin-bottom: 25px;
    @media (min-width: $bp2) {
      flex: 0 0 50%;
    }
  }
}
.text-footer {
  padding: 0 150px 0 10px;
  p {
    font-size: 14px;
    color: #fff;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  span {
    color: $orange;
    margin-top: 55px;
  }
  @media (max-width: $bp2) {
    padding: 10px;
    text-align: center;
  }
}
.info-footer {
  &__columns {
    display: flex;
    justify-content: center;
    margin-bottom: 45px;
    @media (max-width: 500px) {
      flex-wrap: wrap
    }
  }
  &__attention {
    display: flex;
    justify-content: center;
  }
}
.col-info {
  padding: 0 20px;
  text-align: center;
  @media (max-width: 500px) {
    flex: 0 0 100%;
    margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  &__header {
    font-size: 18px;
    line-height: 1;
    color: $orange;
    text-shadow: 1px 1px 1px #000;
    margin-bottom: 15px;
  }
  &__link {
    font-size: 14px;
    color: #fff;
    @media (min-width: $bp2) {
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
.attention-info {
  max-width: 500px;
  min-height: 125px;
  border: 1px solid;
  border-image: linear-gradient(45deg, #fff 0%, rgba(255,255,255,0.5) 50%, #fff 100%) 30;
  border-radius: 5px;
  padding: 20px 10px 20px 65px;
  background: url("../assets/images/icons/expoint.png") left 20px top 20px no-repeat;
  &__text {
    font-size: 14px;
    color: #fff;
  }
}
.footer-footer {
  border-top: 2px solid $violet;
  &__inner {
    padding: 20px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media (max-width: $bp3) {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }
  &__copyright {
    display: flex;
    align-items: center;
    img {
      flex: 0 0 95px;
      width: 95px;
      position: relative;
      top: -3px;
    }
    span {
      color: $orange;
      font-size: 14px;
      line-height: 1;
      margin-left: 5px;
    }
    @media (max-width: $bp3) {
      margin-bottom: 10px;
    }   
  }
  &__contacts {
    display: inline-block;
    color: $orange;
    font-size: 14px;
    a {
      font-size: inherit;
      color: inherit;
      @media (max-width: $bp4) {
        display: block;
        margin-bottom: 10px;
      } 
    }
    span {
      margin: 0 10px;
      @media (max-width: $bp4) {
        display: none
      } 
    }
  }
}
