.product-page {
  &__main {
    padding-top: 30px;
    margin-bottom: 20px;
  }
  &__marketplace {
    margin: 20px 0
  }
  &__info {
    margin-bottom: 20px;
  }
}
.main-product {
  display: grid;
  grid-template-columns: 52% auto;
  grid-template-rows: 60px auto;
  gap: 0 20px;
  &__header {
    text-align: center;
  }
  &__title {
    font-size: 20px;
    font-weight: 900;
    color: $violet;
    line-height: 1.5;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
  &__images {
    grid-row: 1/3;
  }
  @media (max-width: $bp2) {
    grid-template-rows: 60px auto auto;
    grid-template-columns: 100%;
    gap: 20px 0;
    &__header {
      text-align: left;
    }
    &__images {
      grid-row: 2/3;
    }
  }
  @media (max-width: 600px) {
    &__title {
      font-size: 16px;
    }
  }
}
.images-product {
  display: flex;
  align-items: center; 
  &__slider {
    height: 358px;
    flex: 1 1 auto;
    margin-right: 10px;
    min-width: 0;
    overflow: hidden;
    .swiper-wrapper {
      display: flex;
      height: 100%;
    }
  }
  &__item {
    flex-basis: 100%;
    flex-grow: 0;
    flex-shrink: 0;
    cursor: pointer;
  }
  &__image {
    height: 100%;
    width: 99.5%;
    margin: auto;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      max-width: 100%;
      max-height: 100%;
      margin: auto;
    }
  }
  &__subslider {
    height: 288px;
    flex: 0 0 66px;
    min-width: 0;
    overflow: hidden;
    .swiper-wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      @media (max-width: 600px) {
        flex-direction: row
      }
    }
  }
  &__subitem {
    flex-basis: calc(25% - 6px);
    margin-bottom: 8px;
    flex-grow: 0;
    flex-shrink: 0;
    cursor: pointer;
  }
  &__subimage {
    border: 2px solid $maincolor;
    height: 100%;
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  .swiper-slide-thumb-active {
    .images-product__subimage {
      border: 2px solid $violet;
      box-shadow: inset 0 0 3px $violet;
    }
  }
  @media (max-width: $bp2) {
     width: 100%;
     max-width: 640px;
     margin: 0 auto;
  }
  @media (max-width: 600px) {
    flex-direction: column;
    &__slider{
      width: 100%;
      margin-right: 0;
      height: 258px;
      margin-bottom: 15px;
    }
    &__subslider {
      width: 288px;
    }
    &__subitems {
      flex-direction: row;
    }
    &__subitem {
      margin-bottom: 0;
      margin-right: 8px;
    }
  }
}
.details-product {
  min-height: 298px;
  padding: 10px 25px;
  background: $maincolor;
  color: #fff;
  width: 100%;
  max-width: 470px;
  margin: 0 auto;
  border-radius: 5px;
  font-size: 17px;
  &__title {
    margin-bottom: 25px;
    text-align: center;
  }
  &__item {
    line-height: 1.8;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #697579;
    span {
      color: $orange;
      text-shadow: 1px 1px 1px #000
    }
  }
  @media (max-width: $bp2) { 
    margin: 0 -15px;
    padding-bottom: 25px;
    width: calc(100% + 30px);
    max-width: calc(100% + 30px);
    border-radius: 0;
    font-size: 14px; 
  }
}
.info-product {
  &__navitem {
    font-size: 14px;
    font-weight: 700;
    color: $violet;
    line-height: 2;
    padding: 0 30px;
    border: 2px solid $violet;
    transition: all .2s linear;
    cursor: pointer;
    text-align: center;
    &.active {
      background-color: $violet;
      color: #fff;
    }
  }
  @media (min-width:$bp4) {
    &__nav {
      display: flex;
    }
    &__navitem {
      margin-right: 7px;
    }
  }
}
.block-info {
  display: none;
  padding: 20px 0px;
  &__col {
    margin-bottom: 15px;
  }
  &__title {
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 5px;
  }
  &__item {
    font-size: 14px;
    line-height: 2;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #e5e5e5
  }
  &__footer {
    text-align: right;
    font-size: 12px;
  }
  &.active {
    display: block;
  }
  @media (max-width:$bp2) {
    padding: 10px 0px;
  }
  @media (min-width:$bp3) {
    &__cols {
      column-count: 2;
      
      margin: 0 -25px;
    }
    &__col {
      flex: 0 0 50%;
      padding: 0 25px;
      margin-bottom: 15px;
      break-inside: avoid;
    }
  }
}
.marketplace {
  &__list {
    display: flex;
    flex-wrap: wrap
  }
  &__item {    
    border: 1px solid $violet;
    border-radius: 3px;
    margin-right: 13px;
    margin-bottom: 15px;
    &:last-child {
      margin-right: 0; 
      min-width: 80px; 
    }
    a {
      display: flex;
    }
    span {
      display: inline-block;
      line-height: 24px;
      padding: 0 10px;
      font-size: 17px;
      color: $violet;
      white-space: nowrap;
      &:first-child {
        background-color: $violet;
        color: #fff
      }
    }
  }
}

