.rub:after {
  content: "₽";
}
html,body {
 scroll-behavior: smooth !important;
 scrollbar-color: $violet #fff;
 &::-webkit-scrollbar {
   width: 5px
 }
 &::-webkit-scrollbar-track {
   background-color: #fff
 }
 &::-webkit-scrollbar-thumb {
   background-color: $violet
 }
}
body {
  margin: 0;
  font-family: "Roboto";
  font-weight: 400;
  line-height: 1.2;
  color: $maincolor;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.no-scroll {
  overflow: hidden;
}
.container {
  width: 100%;
  max-width: 1264px;
  padding: 0 15px;
  margin: 0 auto;
  @media (max-width: $bp2) {
    padding: 0 10px
  }
}
.page {
  background-color: #fff;
  &__recommendation {
    background-color: #fff;
    margin-bottom: -1px;
  }
  &__tags {
    margin-bottom: 20px;
    background-color: #fff;
  }
}
.btn {
  display: inline-flex;
  color: #fff;
  background: $violet;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  transition: all .5s ease;
  cursor: pointer;
}

.select-custom {
  width: 100%;
  max-width: 235px;
  height: 30px;
  position: relative;
  z-index: 2;
  margin: 0 0 30px auto;
  &.active {
    .select-custom__options {
      display: block;
    }  
    .select-custom__selected {
      border-bottom-right-radius: 0
    }
  }
  &__selected, 
  &__option {
    font-size: 16px;
    padding: 0 13px;
    line-height: 1.8;
  }
  &__selected {
    cursor: pointer;
    background-color: $violet;
    color: #fff;
    font-weight: 900;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:after {
      content: "";
      display: block;
      width: 18px;
      height: 18px;
      background: url("../assets/images/icons/arrowdrop.svg") center no-repeat;
      transition: all .4s ease;
    }
    &.active {
      &:after {
        transform: rotate(180deg)
      }
    }
  }
  &__options {
    //display: none;
    overflow: hidden;
    height: 0;
    background-color: $maincolor;
    color: #fff; 
    font-weight: 500;
    position: absolute;
    top: 100%;
    left: 15px;
    right: 0.5px;
    z-index: -1;
    margin-top: -2px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    transition: all .3s ease;
  }
  &__option {
    border-bottom: 1px solid $violet;
    cursor: pointer;
    display: flex;
    align-items: center;
    &:after {
      content: "";
      display: block;
      width: 10px;
      height: 13px;
      background: url(../assets/images/asc-desc.png) top center no-repeat;
      margin-left: 10px;
    }
    &--desc {
      &:after {
        transform: rotate(180deg)
      }
    }
    &:last-child {
      border-bottom: 0;
    }
    &.active, &:hover {
      background-color: $orange;
    }
  }
}
  