.notfound {
  width: 100%;
  height: calc(100vh - var(--header-inner));
  &__container {
    max-width: 800px;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
    span {
      font-size: 72px;
      font-weight: 700;
      margin-bottom: 50px;
    }
    @media (max-width: $bp3) {
      font-size: 18px;
    }
  }
}