.preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: transform .4s ease;
  background-color: $maincolor;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
  &__inner { 
    position: relative;
  }
  &__bar {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 5px;
    background-color: #126a88// $orange
  }
  &__progress {
    width: 100%;
    transform: translateX(-100%);
    height: 100%;
    background-color: $red
  }
  &__percent {
    position: absolute;
    top: -25px;
    font-weight: 900;
    letter-spacing: 3px;
    font-size: 16px;
    color: $red;
  }
  &__text-stroke,
  &__text-fill {
    font-size: 55px;
    font-weight: 900;
    letter-spacing: 10px
  }
  &__text-stroke {
    -webkit-text-stroke: 1px #fff;
    color: transparent
  }
  &__text-fill {
    position: absolute;
    bottom: 0;
    left: 0;
    color: #fff ;
    clip-path: inset(100%) 
  }
}