.tags-page {
  &__info {
    background-color: $orange;
    padding: 15px 10px;
    font-size: 18px;
    color: #fff;
    font-weight: 700;
    margin-bottom: 10px;
  }
}
.tags {
  padding: 5px 0 15px;
  &__items {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin: 10px 0 20px 0;
  }
  &__item {
    cursor: pointer;
    font-size: 14px;
    line-height: 1.4;
    border: 2px solid $maincolor;
    padding: 0 4px;
    margin: 4px;
    white-space: nowrap;
    transition: all .3s linear;
    @media (min-width: $bp2) {
      &:hover {
        background-color: $orange;
        border: 2px solid $orange;
        color: #fff;
      }
    }
    &.active {
      background-color: $orange;
      border: 2px solid $orange;
      color: #fff;
    }
    &.disable {
      opacity: 0.5;
      pointer-events: none;
    }
  }
}
.catalog-tags {
  &__items {
    max-width: 1144px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
    @media (max-width: $bp2) {
      max-width: 870px;
      grid-template-columns: 1fr 1fr 1fr;
    }
    @media (max-width: $bp3) {
      max-width: 562px;
      grid-template-columns: 1fr 1fr;
    }
    @media (max-width: $bp4) {
      max-width: 271px;
      grid-template-columns: 1fr
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    margin: 20px 0;
    .btn {
      padding: 7px 15px;
      margin: 11.5px 0;
      transition: all .5s ease;
      &:hover {
        background-color: $orange
      }
    }
    img {
      width: 52px;
      height: 52px
    }
    
  }
}
@keyframes rotate {
  0% {
    transform: rotate(0deg)
  }
  100% {
    transform: rotate(360deg)
  }
}