:root {
  --header-inner: 80px;
}
.header {
  background-color: #fff;
  &__inner {
    height: 80px;
    display: flex;
    align-items: center;
  }
  &__logo {
    flex: 0 0 190px;
  }
  &__menu {
    flex: 1 1 auto;
  }
}
.logo-header {
  &__logo {
    img {
      max-width: 100%;
    }
  }
  &__text {
    font-size: 11px;
  }
}
.menu {
  position: relative;
  &__body {
    @media (max-width: $bp2) {
      visibility: hidden;
      opacity: 0;
      position: fixed;
      background-color: $maincolor;
      top: var(--header-inner);
      left: 0;
      width: 100%;
      bottom: 0;
      overflow: scroll;
      z-index: 100;
      transition: all .3s ease;
      &.active {
        visibility: visible;
        opacity: 1;
        a {
          animation: translateY .5s linear both;
          animation-delay: calc(var(--item-index)*0.2s);
          animation-timing-function: cubic-bezier(.25,.1,.25,1);
        }
      }
    }
  }
  &__list { 
    display: flex;
    justify-content: center;
    align-items: center;  
    @media (max-width: $bp2) {
      display: block;
      li {
        overflow: hidden;
        margin: 30px;  
      }
    }
    @media (max-width: $bp4) {
      li {
        margin: 30px 10px;  
      }
    }
  }
  &__link {
    display: inline-block;
    padding: 0 10px;
    font-size: 35px;
    color: #fff;
    transition: all .2s ease;
    @media (min-width: $bp2) {
      color: $maincolor;
      font-size: 18px;
      font-weight: 700;
      &:hover,&.active {
        color: $orange;
      }
    }
    @media (max-width: $bp4) {
      font-size: 25px;
    }
  }
  &__icon {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    z-index: 2;
  }
}
.icon-menu {
  display: none;
  @media (max-width: $bp2) {
    display: flex;
    width: 27px;
    height: 20px;
    flex-direction: column;
    justify-content: space-between;
    &__item, &::before, &:after{
      width: 25px;
      height: 3px;
      background-color: #000;
      transition: all .3s ease;
    }
    &::before, &:after {
      content: "";
    }
    &.active {
      justify-content: center;
      .icon-menu__item {
        transform: scale(0)
      }
      &::before {
        position: absolute;
        transform: rotate(45deg)
      }
      &::after {
        position: absolute;
        transform: rotate(-45deg)
      }
    }
    
  }
}
