.pagination {
  display: flex;
  align-items: center;
  &__items {
    display: flex;
    align-items: center;
    margin: 0 5px;
  }
  &__item {
    cursor: pointer;
    &.active {
      .pagination__link {
        background-color: $orange;
        color: #fff;
      }
    }
    &.disable {
      pointer-events: none
    }
  }
  &__link {
    color: $violet;
    font-size: 16px;
    font-weight: 900;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    transition: all .5s ease;
    &:hover {
      color: $orange;
    }
  }
  &__arrow {
    width: 10px;
    height: 10px;
    border-left: 2px solid $violet;
    border-bottom: 2px solid $violet;
    background-color: transparent;
    cursor: pointer;
    transition: all .5s ease;
    &:hover {
      border-left: 2px solid $orange;
      border-bottom: 2px solid $orange;
    }
    &:disabled {
      opacity: 0.5
    }
  }
  &__arrow-prev {
    transform: rotate(45deg);
  }
  &__arrow-next {
    transform: rotate(-135deg);
  }
  @media (max-width: $bp4) {
    justify-content: center;    
  }
}